import React from "react";
import {Link} from "react-router-dom"
import { Container, Button } from 'react-bootstrap';
import "./App.css";

function Main(){
    return (
        <div className="center_vertical">
        <Container fluid>  
      <Button variant="outline-secondary" size="lg" className="mr-5 btn"><Link className="link btn-lg" to="/find">Find</Link></Button>
      <Button variant="outline-secondary" size="lg" className="btn" ><Link className="link btn-lg" to="/create">Create</Link></Button>
      </Container>
      </div>
    )
}

export default Main;