import React, {useState, useEffect} from "react";
import axios from "axios";
import {Form, Button, Container, Row, Col} from "react-bootstrap";
import history from '../history';
import { useForm } from "react-hook-form";
import { faArrowLeft, faTrashAlt } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {Link} from "react-router-dom";
import Query from "./Query";
import "./App.css";
import API_HOST from '../constants';

function Find() {
  
  const [name, setName] = useState({input: ''});
  const [carrier, setCarrier] = useState([]);
  const [isEmpty, setIsEmpty] = useState(false);
  const [isClear, setIsClear] = useState(false);
  const { register, handleSubmit, errors } = useForm();


  useEffect(() => {
    const parsedCarrier = JSON.parse(localStorage.getItem("carrier") || 0);
    setCarrier(parsedCarrier);
    const parsedCarrier2 = JSON.parse(localStorage.getItem("isClear") || 0);
    setIsClear(parsedCarrier2);
   }, []);

  useEffect(() => {
    localStorage.setItem("carrier", JSON.stringify(carrier));
    localStorage.setItem("isClear", JSON.stringify(isClear));
  }, [carrier, isClear]);


  function handleClick(){
    setIsClear(true);
  }


  function handleChange(e){

    const input = {input: e.target.value};
    setName(input);
    setIsEmpty(false);
  
  }

  function onSubmit(e){
    
    axios.get(`${API_HOST}/find?q=${name.input}`).then(res => {

        if(res.data.length === 0){
          setIsEmpty(true);
        }else{
         handleResult(res.data);
        }
      });
      
      history.push({
        pathname: `/find?q=${name.input}`
      });

      setName({input: ''});
      setIsClear(false);   
  }

  function handleResult(data){
  
     setCarrier(() => {
       return data.map(item => {
         return (
           {
            id: item.id,
            name: item.name,
            origin: item.origin,
            registration: item.registration  
         }
         )
       })
     })   
  }

    return (
        <div className="center_vertical">
        <Container fluid>
        <Row className="justify-content-center">
        <Col sm={6}>
      <div className="wrapper_links">
        <div className="search_bar text-left">
        <Link to="/create"><FontAwesomeIcon className="icon" icon={faArrowLeft} /></Link>
          <Link to="/create"><p>Create</p></Link>
        </div>
        <div className="search_bar text-right" onClick={handleClick}>
          <p>Clear</p>
          <FontAwesomeIcon className="icon" icon={faTrashAlt} />
        </div>
      </div>
       <Form className="p-4 input-form" autoComplete="off" onSubmit={handleSubmit(onSubmit)}>
  <Form.Group controlId="formBasicText">
    <Form.Label>Name</Form.Label>
    <Form.Control type="text" name="name" value={name.input} onChange={handleChange} ref={register({ required: true })} placeholder="Name" size="lg" />
  </Form.Group>
  <div className="text-left">
    {errors.name && <span style={{"color": "red"}}>This field is required</span>}
    </div>
  
  <Button variant="outline-secondary" type="submit" size="lg">
    Find
  </Button>
</Form>

{isEmpty && !isClear && (<h3 style={{color: "red"}}>There is no such a carrier</h3>)}

{(isEmpty || isClear) ? null : <div className="carrier_info">
{carrier.map((carrier, index) => (
  <Query 
   key={index}
   id={carrier.id}
   name={carrier.name}
   origin={carrier.origin}
   registration={carrier.registration}
  />
  )
)}
</div>}

</Col>
</Row>
</Container>
        </div>
    )
}

export default Find;