import React, { useState } from "react";
import { useForm } from "react-hook-form";
import axios from "axios";
import {Form, Button, Container, Row, Col} from "react-bootstrap";
import "./App.css";
import { faArrowLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {Link} from "react-router-dom";
import API_HOST from '../constants';

function Create (){
   
    const { register, handleSubmit, errors } = useForm();
    const [input, setInput] = useState({
        name: '',
        origin: '',
        registration: ''
    });
    const [result, setResult] = useState(false);

   
    function handleChange(event){
       const {name, value} = event.target;
       setInput(prevInput => {
         return {
           ...prevInput,
           [name]: value    
       }
      })

      setResult(false);
    };

 function onSubmit(event){

      const newInput = {
        name: input.name,
        origin: input.origin,
        registration: input.registration
      };

      setResult(true);

    axios.post(`${API_HOST}/create`, newInput)
      .then(function (response) {
        console.log(response);
      })
      .catch(err => console.log(err));


      setInput({ 
       name: '',
       origin: '',
       registration: ''
      })
    };


    return (
        <div className="center_vertical">
        <Container fluid>
        <Row className="justify-content-center">
        <Col sm={6}>
        <div className="search_bar text-left">
        <Link to="/find"><FontAwesomeIcon className="icon" icon={faArrowLeft} /></Link>
          <Link to="/find"><p>Find</p></Link>
        </div>
       <Form className="p-4 pb-5 input-form" autoComplete="off" onSubmit={handleSubmit(onSubmit)} >
  <Form.Group controlId="formBasicName">
    <Form.Label>Name</Form.Label>
    <Form.Control type="text" name="name" value={input.name} ref={register({ required: true })}  onChange={handleChange} placeholder="Name" size="lg"/>
    <div className="text-left">
    {errors.name && <span style={{"color": "red"}}>This field is required</span>}
    </div>
  </Form.Group>
  <Form.Group controlId="formBasicOrigin">
    <Form.Label>Origin</Form.Label>
    <Form.Control type="text" name="origin" ref={register} value={input.origin} onChange={handleChange} placeholder="Origin" size="lg" />
  </Form.Group>
  <Form.Group controlId="formBasicRegistration">
    <Form.Label>Registration Code</Form.Label>
    <Form.Control type="text" name="registration" value={input.registration} onChange={handleChange} placeholder="Registration Code" size="lg" />
  </Form.Group>
   
  <Button className="submit_button " variant="outline-secondary" type="submit" size="lg">
    Create
  </Button>

</Form>
{result && (<h3 style={{color: "red"}}>Successfully added</h3>)}
</Col>
</Row>
</Container>
        </div>
    )
}

export default Create;






